<template>
    <el-container>
        <el-header>
            水位模型管理
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-form label-width="10%">
                    <el-form-item label="水库名称">
                        <el-select v-model="selectedReservoir"
                                   placeholder="未选择">
                            <el-option
                                    v-for="item in reservoirList"
                                    :label="item.name"
                                    :value="item.name"
                                    :key="item.id">
                                {{item.name}}
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-table v-show="selectedReservoir!==''" :data="models">
                    <el-table-column property="name" label="模型名称" width="100"></el-table-column>
                    <el-table-column label="开始日期" width="150">
                        <template slot-scope="scope">
                            <span>{{getDateStringFromTimestamp(scope.row.firstDate)}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column property="orders" label="参数（最左为0阶）"></el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            width="100">
                        <template>
                            <el-button type="text" size="small">编辑</el-button>
                            <el-button type="text" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-card>

        </el-main>
    </el-container>
</template>

<script>
    export default {
        name: "EditLevelModel",
        data: function () {
            return{
                reservoirList: [],
                selectedReservoir: '',
                models:[],
            }
        },
        mounted(){
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;

                } else {
                    console.log("error")
                }
            })
        }
    }
</script>

<style scoped>
    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
</style>
